import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { CircularProgress, Snackbar } from '@material-ui/core';
import axios from 'axios';
import formData from './config/ca.json';

class AddressForm extends Component{
  constructor(props){
    super(props);
    this.state={
      submitLoading: false,
      errorMessage: '',
      error: false,
      submitSuccess: false,
      formError:{
        name: '',
        email: '',
        mobile: '',
        whatsapp: '',
        institute: '',
        city: '',
        state: '',
        zip: ''
      },
      values:{
        name: '',
        email: '',
        mobile: '',
        whatsapp: '',
        institute: '',
        city: '',
        state: '',
        zip: ''
      }
    };
  }

  onFormChange(val, field){
    console.log(val);
    this.setState(prevState =>{
      return ({
        ...prevState,
          values:{
            ...prevState.values,
            [field]: val
        }
      });
    });
  }

  handleSubmission(){
    this.setState({
      submitLoading: true
    });
    axios({
      method: 'POST',
      url: '/ca/register',
      data: this.state.values
    }).then((response)=>{
      console.log(response);
      this.setState({
        submitLoading: false,
        submitSuccess: true
      });
    }).catch((e)=>{
      console.log(e);
      this.setState({
        submitLoading: false,
        error: true,
        errorMessage: 'E-Mail already registered'
      });
    });
  }

  render(){
    const { values, submitSuccess } =this.state;
    if(!submitSuccess){
      return (
        <React.Fragment>
          <Grid item xs={12} sm={12}>
            <Typography variant="h6" gutterBottom align="left">
              Contact Information
            </Typography>
          </Grid>
          <Grid container spacing={3}>
            {formData.map((form)=>{
              if(form.type==='information'){
                return(
                  <Grid item xs={12} sm={12} key={form.name}>
                    <Typography variant="h6" gutterBottom align="left">
                      {form.text}
                    </Typography>
                  </Grid>
                );
              }
              return(
                <Grid item xs={form.grid.xs} sm={form.grid.sm} key={form.name}>
                  <TextField
                    required={form.required}
                    name={form.name}
                    label={form.label}
                    value={values[form.name] || ''}
                    fullWidth={form.fullWidth}
                    onChange={(event)=>{
                      this.onFormChange(event.target.value, form.name);
                    }}
                  />
                </Grid>
              )
            })}
          </Grid>
      
          <div style={{ marginTop: '4vh' }}>
            <Button
              variant="contained"
              color="primary"
              onClick={()=>{
                this.handleSubmission();
              }}
            >
              {this.state.submitLoading && (<CircularProgress style={{ color: "#fff" }} />)}
              {!this.state.submitLoading && ("Register")}
            </Button>
          </div>
          <Snackbar
            open={this.state.error}
            anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
            message={this.state.errorMessage}
            variant="error"
            onClose={()=> {
              this.setState({
                error: false
              })
            }}
            autoHideDuration={5000}
          />
        </React.Fragment>
      );
    }
    else{
      return(
        <React.Fragment>
          <Grid item xs={12} sm={12}>
            <Typography variant="h5" gutterBottom align="center">
              Successfully Registered
            </Typography>
            <Typography variant="h6" gutterBottom align="center">
              Our Team Will Reach You Soon
            </Typography>
          </Grid>
        </React.Fragment>
      );
    }

  }
}

export default AddressForm;
